<template>
	<view>
		<navigator url="/pages/home/editMessage">
			<view class="editmsg cu-form-group">
				<input disabled="disabled" id="edit" placeholder="留个言吧..." name="input"></input>
				<view id="msgicon" class="title cuIcon-edit"></view>
			</view>
		</navigator>
		<view v-for="(item,index) in messageList">
			<view class="cu-card dynamic">
				<view class="cu-item shadow">
					<view class="cu-list menu-avatar">
						<view class="cu-item">
							<view class="cu-avatar round lg" style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg);"></view>
							<view class="content flex-sub">
								<view>
									<span>{{name}}</span>
								</view>
								<view class="text-gray text-sm flex justify-between">
									{{item.createTime}}
									<span v-if="item.status==1">已采纳</span>
								</view>
							</view>
						</view>
					</view>
					<view class="text-content margin-top">
						{{item.content}}
					</view>
				</view>
			</view>

		</view>
	</view>
</template>

<script>
	import messageApi from '../../api/message.js'
	import mineAPI from '../../api/mine.js'
	import { mapState } from 'vuex'
	
	export default {
		data() {
			return {
				messageList: [],
				name:''
			}
		},
		// 监听页面加载
		onLoad() {
			mineAPI.getOwnerInfo(this.userInfo.userId).then(res=>{
				if(res.code === 200){
						this.name = res.data.name;
					}
				})
			this.initMessageList();
			setTimeout(function() {
				console.log('start pulldown');
			}, 1000);
			uni.startPullDownRefresh();
		},
		// 监听下拉刷新
		onPullDownRefresh() {
			this.initMessageList();
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		computed:{
			 ...mapState(['userInfo'])
		},
		methods: {
			// 初始化评价留言列表
			initMessageList() {
				console.log(this.userInfo.userId);
				messageApi.messageList(this.userInfo.userId).then(res => {
					console.log(res);
					if (res.code === 200) {
						this.messageList = res.data;
						console.log(res.data);
					}
				})
			},
			// 移除标签
			removeTAG(html) {
				return this.$tagutil.removeTAG(html);
			}
		}
	}
</script>

<style>
	#msgicon {
		font-size: 20px;
		padding-left: 2%;
	}

	.editmsg {
		height: 60px;
	}
	
	#edit{
		background-color: #F1F1F1;
		border-radius: 15px;
		height: 40px;
		line-height: 40px;
		padding-left: 10px;
	}
</style>
